.achListBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}

.achList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    list-style: none;
    padding: 0;
    margin: 0;
}

#leaderHeader {
    display: flex;
    flex-direction: column;
    align-content: center;
    padding-left: 10px;
}

.leaderHeaderText {
    margin: 0 0 10px 0;
}

.leaderSelectionText {
    margin: 0
}

.listHeader {
    padding-left: 10px;
    margin-bottom: 10px;
}

.leaderImage {
    height: 64px;
    width: 64px;
    margin-right: 10px;
}

.leaderSelection {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: "Times New Roman", Times, serif;
}

